*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  box-sizing: border-box;
}

div {
  margin: 0;
}

p {
  padding: 0;
  margin: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  padding: 0;
  margin: 0;
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

body {
  font-size: 24px;
  line-height: 32px;
  color: #181818;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  padding-top: 70px;
  overflow: hidden;
}

.container {
  max-width: 1259px;
  padding: 0 15px;
  margin: 0 auto;
}

.section-outer {
  margin-bottom: 120px;
}
.section-outer--small {
  margin-bottom: 100px;
}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 40px;
  font-size: 55px;
  line-height: 55px;
  text-align: center;
  letter-spacing: 0.01em;
  font-family: "Arno Pro Display", sans-serif;
  font-weight: 300;
}
.title::before, .title::after {
  width: 60vw;
  height: 2px;
  margin: 0 60px;
  content: "";
  background: #dfd7d3;
}
.title::before {
  margin-left: -20vw;
}
.title::after {
  margin-right: -20vw;
}
.title__text {
  width: 100%;
  max-width: 650px;
}

.btn {
  display: block;
  max-width: 100%;
  padding: 24px;
  cursor: pointer;
  background: none;
  border: none;
}
.btn .btn-text {
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  font-family: "Circle", sans-serif;
  font-weight: 400;
}
.btn-no-bg {
  position: relative;
  width: 400px;
  color: #fff;
  border: 2px solid #dbdbdb;
  border-right: none;
  transition: all 0.3s ease-in-out;
}
.btn-no-bg .btn-text {
  position: relative;
}
.btn-no-bg .btn-text::before {
  position: absolute;
  top: 50%;
  bottom: 50%;
  left: calc(100% - -18px);
  width: 66px;
  height: 10px;
  content: "";
  background: url("../images/icons/arrow-right.svg") no-repeat;
  transition: all 0.3s ease-in-out;
  transform: translateY(-50%);
}
.btn-no-bg::before, .btn-no-bg::after {
  position: absolute;
  right: 0;
  width: 2px;
  height: 30%;
  content: "";
  background-color: #dbdbdb;
  transition: all 0.3s ease-in-out;
}
.btn-no-bg::before {
  top: 0;
}
.btn-no-bg::after {
  bottom: 0;
}
.btn-no-bg:hover {
  border-color: #ca6d38;
}
.btn-no-bg:hover::before, .btn-no-bg:hover::after {
  background-color: #ca6d38;
}
.btn-no-bg:hover .btn-text::before {
  left: calc(100% - -30px);
}
.btn-no-bg:active {
  border-color: #dbdbdb;
}
.btn-no-bg:active::before, .btn-no-bg:active::after {
  background-color: #dbdbdb;
}
.btn-no-bg:active .btn-text::before {
  left: calc(100% - -10px);
}
.btn-bg {
  width: 400px;
  color: #181818;
  background-color: #dfd7d3;
  transition: all 0.3s ease-in-out;
}
.btn-bg:hover {
  box-shadow: 0 0 30px rgba(223, 215, 211, 0.2);
}
.btn-bg:active {
  box-shadow: 0 0 15px rgba(223, 215, 211, 0.1);
}
.btn-standart {
  width: 275px;
  border: 2px solid #dfd7d3;
  transition: all 0.3s ease-in-out;
}
.btn-standart .btn-text {
  color: #454545;
}
.btn-standart:hover {
  border-color: #ca6d38;
}
.btn-standart:active {
  opacity: 0.8;
}

.logo__img {
  width: 230px;
  height: 41px;
}

.menu__list {
  display: flex;
}
.menu__list-item + .menu__list-item {
  margin-left: 34px;
}
.menu__list-link {
  font-size: 20px;
  line-height: 25px;
  font-family: "Arno Pro Display", sans-serif;
  font-weight: 300;
}
@media (any-hover: hover) {
  .menu__list-link {
    transition: all 0.3s ease-in-out;
  }
  .menu__list-link:hover {
    color: #ca6d38;
  }
}

.burger-btn {
  position: relative;
  display: none;
  width: 31px;
  height: 12px;
  margin-left: auto;
  cursor: pointer;
  cursor: pointer;
  background: none;
  border: none;
}
.burger-btn__line {
  position: absolute;
  top: 50%;
  right: 0;
  bottom: 50%;
  width: 100%;
  height: 2px;
  background-color: #ca6d38;
  transition: all 0.3s ease-in-out;
  transform: translateY(-50%);
}
.burger-btn::before, .burger-btn::after {
  position: absolute;
  right: 0;
  height: 2px;
  content: "";
  background-color: #ca6d38;
  transition: all 0.3s ease-in-out;
}
.burger-btn::before {
  top: 0;
  width: 100%;
}
.burger-btn::after {
  bottom: 0;
  width: 70%;
}
@media (any-hover: hover) {
  .burger-btn:hover::after {
    width: 100%;
  }
}

.contact-phone {
  position: relative;
  display: flex;
  align-items: center;
}
.contact-phone__img {
  position: absolute;
  top: -2px;
  width: 15px;
  height: 15px;
}
.contact-phone__text {
  padding-left: 25px;
  font-size: 24px;
  line-height: 25px;
  font-family: "Arno Pro Display", sans-serif;
  font-weight: 400;
}
@media (any-hover: hover) {
  .contact-phone {
    transition: all 0.3s ease-in-out;
  }
  .contact-phone:hover svg {
    fill: #ca6d38;
  }
  .contact-phone:hover {
    color: #ca6d38;
  }
}

.social-item {
  position: relative;
  display: flex;
}
.social-item__img {
  position: absolute;
  top: -30%;
  left: 0;
}
.social-item__img svg {
  width: 15px;
  height: 15px;
}
.social-item__img svg path {
  fill: #fff;
  transition: all 0.3s ease-in-out;
}
.social-item__text {
  padding-left: 30px;
  font-size: 22px;
  line-height: 25px;
  color: #f3f1ef;
  transition: all 0.3s ease-in-out;
  font-family: "Arno Pro Display", sans-serif;
  font-weight: 400;
}
@media (any-hover: hover) {
  .social-item__text:hover {
    color: #ca6d38;
  }
  .social-item:hover path {
    fill: #ca6d38;
  }
}

.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  background-color: #fff;
}
.header--active .header-navigation {
  flex-direction: column;
  align-items: center;
  height: 100vh;
  padding-bottom: 53px;
  color: #fff;
  visibility: visible;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}
.header--active .header-navigation::before {
  position: absolute;
  width: 100%;
  height: 100%;
  content: "";
  background: url("../images/default/mobile-menu-bg.png") no-repeat;
  background-size: cover;
}
.header--active .header-contact {
  display: block;
  margin-left: 0;
  font-size: 24px;
  line-height: 25px;
}
.header--active .menu {
  padding: 40px 64px 53px 64px;
}
.header--active .menu__list {
  flex-direction: column;
}
.header--active .menu__list-item {
  z-index: 1;
  text-align: center;
}
.header--active .menu__list-item + .menu__list-item {
  margin-top: 30px;
  margin-left: 0;
}
.header--active .menu__list-link {
  width: 100%;
}
.header--active .burger-btn__line {
  visibility: hidden;
  opacity: 0;
}
.header--active .burger-btn::before {
  top: 45%;
  transform: rotate(45deg);
}
.header--active .burger-btn::after {
  bottom: 45%;
  width: 100%;
  transform: rotate(-45deg);
}
.header-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 70px;
}
.header-navigation {
  display: flex;
  transition: all 0.3s ease-in-out;
}
.header-contact {
  margin-left: 35px;
}
.header-contact .contact-phone__img path {
  fill: #ca6d38;
}

.footer {
  padding: 54px 0 62px 0;
  margin-top: auto;
  background-color: #303030;
}
.footer-inner {
  display: flex;
  justify-content: space-between;
  color: #fff;
}
.footer-contact {
  display: inline-block;
}
.footer-contact.contact-phone path {
  fill: #fff;
  transition: all 0.3s ease-in-out;
}
.footer-contact .contact-phone__img {
  top: -6px;
  left: 0;
}
.footer-contact__item + .footer-contact__item {
  margin-top: 15px;
}
.footer .contact-phone:hover path {
  fill: #ca6d38;
}
.footer-menu {
  display: flex;
  justify-content: space-between;
  width: 280px;
}
.footer-menu__list {
  width: 100%;
  max-width: 110px;
}
.footer-menu__item + .footer-menu__item {
  margin-top: 20px;
}
.footer-menu__link {
  font-size: 20px;
  line-height: 25px;
  transition: all 0.3s ease-in-out;
  font-family: "Arno Pro Display", sans-serif;
  font-weight: 400;
}
@media (any-hover: hover) {
  .footer-menu__link:hover {
    color: #ca6d38;
  }
}
.footer-copyright-info {
  width: 254px;
  font-size: 18px;
  line-height: 26px;
  color: rgba(255, 255, 255, 0.5);
  font-family: "Circle", sans-serif;
  font-weight: 350;
}
.footer-copyright {
  margin-top: 36px;
}
.footer-confidentiality, .footer-mobile-confidentiality {
  display: inline-block;
  margin-top: 10px;
  border-bottom: 1px solid rgba(133, 103, 103, 0.5);
  transition: all 0.3s ease-in-out;
}
@media (any-hover: hover) {
  .footer-confidentiality:hover, .footer-mobile-confidentiality:hover {
    color: #ca6d38;
  }
}
.footer-mobile-info {
  display: none;
  width: 290px;
  margin-top: 30px;
  font-size: 18px;
  line-height: 26px;
  color: rgba(255, 255, 255, 0.5);
  font-family: "Circle", sans-serif;
  font-weight: 350;
}

.intro {
  position: relative;
  height: calc(100vh - 70px);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
}
.intro::before {
  position: absolute;
  z-index: 0;
  width: 55%;
  height: 100%;
  content: "";
  background: url("../images/default/bg.png");
}
.intro-content {
  position: absolute;
  top: 50%;
  width: 40%;
  color: #fff;
  transform: translateY(-50%);
}
.intro__title {
  width: 100%;
  max-width: 630px;
  font-size: 65px;
  line-height: 65px;
  font-family: "Arno Pro Display", sans-serif;
  font-weight: 300;
}
.intro__text {
  width: 100%;
  max-width: 495px;
  margin-top: 50px;
  font-size: 24px;
  line-height: 32px;
  font-family: "Circle", sans-serif;
  font-weight: 300;
}
.intro__btn {
  margin-top: 96px;
}

.preference-top {
  margin-bottom: 80px;
}
.preference__text {
  width: 100%;
  max-width: 980px;
  margin: 0 auto;
  font-size: 24px;
  font-weight: 350;
  line-height: 32px;
  text-align: center;
  font-family: "Circle", sans-serif;
  font-weight: 300;
}
.preference-items {
  display: flex;
  justify-content: space-evenly;
}
.preference-item {
  width: 100%;
  max-width: 255px;
}
.preference-item__img {
  margin-bottom: 30px;
}
.preference-item__img img {
  width: 80px;
  height: 80px;
  object-fit: cover;
}
.preference-item__text {
  font-size: 20px;
  line-height: 30px;
  font-family: "Circle", sans-serif;
  font-weight: 400;
}

