@media (max-width: 1200px) {
  .contact-phone__text {
    font-size: 20px;
    line-height: 20px;
  }

  .menu__list-item + .menu__list-item {
    margin-left: 20px;
  }

  .title {
    font-size: 50px;
    line-height: 50px;
  }

  .header-contact {
    margin-left: 25px;
  }
}
@media (max-width: 1105px) {
  .logo__img {
    max-width: 200px;
  }

  .contact-phone__text {
    padding-left: 20px;
  }

  .title {
    font-size: 40px;
    line-height: 40px;
  }

  .menu__list-item + .menu__list-item {
    margin-left: 10px;
  }

  .header-contact {
    margin-left: 20px;
  }
}
@media (max-width: 1000px) {
  .burger-btn {
    display: block;
  }

  .header-navigation {
    position: absolute;
    top: 100%;
    right: 0;
    left: 0;
    z-index: 10;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }

  .intro::before {
    width: 100%;
    height: 55vh;
  }
  .intro-content {
    top: 6%;
    width: 100%;
    transform: translateY(0);
  }
}
@media (max-width: 900px) {
  .title {
    font-size: 30px;
    line-height: 30px;
  }
  .title__text {
    max-width: 450px;
  }
}
@media (max-width: 850px) {
  .preference-top {
    margin-bottom: 25px;
  }
  .preference-items {
    flex-direction: column;
    align-items: center;
  }
  .preference-item {
    text-align: center;
  }
  .preference-item + .preference-item {
    margin-top: 25px;
  }
  .preference-item__img {
    margin-bottom: 15px;
  }
}
@media (max-width: 800px) {
  .title::before, .title::after {
    width: 70vw;
  }

  .footer-inner {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .footer-inner .social-item {
    display: inline-block;
  }
  .footer-item + .footer-item {
    margin-top: 30px;
  }
  .footer-menu {
    flex-direction: column;
    align-items: center;
  }
  .footer-menu__list + .footer-menu__list {
    margin-top: 20px;
  }
  .footer-copyright, .footer-confidentiality {
    display: none;
  }
  .footer-mobile-info {
    display: block;
  }
}
@media (max-width: 700px) {
  .title {
    font-size: 30px;
    line-height: 30px;
  }
  .title::before, .title::after {
    width: 20vw;
  }
  .title__text {
    max-width: 350px;
  }

  .preference .title {
    margin-bottom: 20px;
  }
  .preference__text {
    font-size: 20px;
    line-height: 27px;
  }
}
@media (max-width: 600px) {
  .section-outer,
.section-outer--small {
    margin-bottom: 50px;
  }

  .intro::before {
    height: 65vh;
  }
  .intro-content {
    top: 10%;
  }
  .intro__title {
    max-width: 520px;
    font-size: 50px;
    line-height: 50px;
  }
  .intro__btn {
    margin-top: 40px;
  }

  .preference__text {
    font-size: 18px;
    line-height: 26px;
  }
}
@media (max-width: 500px) {
  .btn {
    padding: 18px 23px;
  }
  .btn-no-bg .btn-text::before {
    width: 35px;
    background: url("../images/icons/arrow-right-small.svg");
  }

  .title {
    font-size: 25px;
    line-height: 25px;
  }
  .title::before, .title::after {
    margin: 0 16px;
  }
  .title::before {
    margin-left: -20vw;
  }
  .title::after {
    margin-right: -20vw;
  }
  .title__text {
    max-width: 250px;
  }
}
@media (max-width: 450px) {
  .btn .btn-text {
    display: block;
    font-size: 14px;
    line-height: 20px;
  }
  .btn-no-bg {
    width: 276px;
  }

  .title::before, .title::after {
    width: 30vw;
  }
  .title::before {
    margin-left: -30vw;
  }
  .title::after {
    margin-right: -30vw;
  }
  .title__text {
    max-width: 165px;
  }

  .intro::before {
    height: 55vh;
  }
  .intro__title {
    font-size: 42px;
    line-height: 42px;
  }
  .intro__text {
    font-size: 20px;
    line-height: 26px;
  }

  .preference__text {
    max-width: 350px;
  }
}
