  .btn-no-bg .btn-text::before {
    left: calc(100% - -7px);
  }

  .intro__title {
    max-width: 259px;
    font-size: 30px;
    line-height: 30px;
  }
  .intro__text {
    max-width: 290px;
    margin-top: 15px;
    font-size: 18px;
    line-height: 26px;
  }
  .intro__btn {
    margin-top: 25px;
  }

  .preference__text {
    max-width: 290px;
  }
}